.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1142px;
  margin: 0 auto;
  margin-top: 32px; }

.page-header__phone {
  font-family: "Fira Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  color: #484848; }

.page-header__callback {
  display: inline-block;
  margin: 0;
  padding: 0;
  background: none;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  font-variant: small-caps;
  text-transform: lowercase;
  line-height: 18px;
  color: #484848;
  border: none;
  border-bottom: 1px dashed #484848;
  cursor: pointer; }

.page-header__logo {
  padding-right: 25px; }
